import { question_info,user_add_answer,answer_info,user_add_comment,answer_comment } from '../../api/api.js'
export default {
	data() {
		return {
			question_id: '',
			answer_id: '',
			access_token: '',
			member_id: '',
			isOpen: false,
			isComment: false,
			commentText: '',
			isAddAnswer: false,
			answerText: '',
			imgList: [],
			videoSrc: '',
			questionDetail: {},
			answerDetail: {},
			tags: [],
			isSeeImg: false,
			iscanPublish: true
		}
	},
	onLoad(option) {
		let that = this
		that.answer_id = option.id
		uni.getStorage({
		    key: 'access_token',
		    success: function (res) {
		        that.access_token = res.data
		    }
		});
		uni.getStorage({
		    key: 'member_id',
		    success: function (res) {
		        that.member_id = res.data
		    }
		});
		console.log(that.member_id)
		console.log(that.access_token)
		that.getAnswerDetail()
		that.getcommentList()
	},
	methods: {
		// 展开图片
		seeImg(){
			this.isSeeImg = true
		},
		// 收起图片
		notSeeImg(){
			this.isSeeImg = false
		},
		//发表评论
		submitComment(){
			let that = this
			user_add_comment({
				member_id: that.member_id,
				access_token: that.access_token,
				answer_id: that.answer_id,
				to_comment_id: '',
				content: that.commentText
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					uni.showToast({
						title: '发布评论成功!',
						duration: 2000,
						icon: 'none'
					});
					that.commentText = ''
					that.isComment = false
					that.getcommentList()
				}
			})
		},
		//获取评论列表
		getcommentList(){
			let that = this
			answer_comment({
				answer_id: that.answer_id,
				page: 1,
				limit: 10
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					that.commentList = res.data.data.list
				}
			})
		},
		//点击上传图片
	   uploadImg(){
		   let that = this	 
			uni.chooseImage({
				count: 3, //上传文件数量
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album','camera'], //从相册选择
				success:(res)=> {
				  console.log(res)
				  if (that.imgList.length != 0) {
					  that.imgList = that.imgList.concat(res.tempFilePaths)
				  } else {
					  that.imgList = res.tempFilePaths
				  } 
			}
		});
	},
	//上传视频
	uploadVideo(){
		let that = this
		uni.chooseVideo({
			count: 1,
			sourceType: ['camera', 'album'],
			success: function (res) {
				console.log(res)
				that.videoSrc = res.tempFilePath;
			}
		})
	},
	//发布回答
	submitAnswer(){
		let that = this
		that.iscanPublish = false
		user_add_answer({
			member_id: that.member_id,
			access_token: that.access_token,
			content: that.answerText,
			question_id: that.question_id
		}).then(res => {
			console.log(res)
			if(res.data.code == 1){
				let post_id = res.data.data
				//若没有图片以及视频
				if(that.imgList.length == 0 && that.videoSrc == ''){
					console.log("没有视频和图片")
					uni.showToast({
						title: '回答成功!',
						duration: 2000,
						icon: 'none'
					});
					//跳转回答详情页
					uni.navigateTo({
						url: '../answerDetail/answerDetail?id='+post_id,
					})
				}
				//如果上传了图片
				if(that.imgList.length > 0 ){
					let imgs = that.imgList.map((value,index)=>{
						return {	
							name:'image'+index,
							uri:value
						}
					});
					console.log(imgs)
					uni.uploadFile({
						url: 'http://test.api.lanchonggk.com/industry/api/add_pics',
						fileType: 'image',
						files: imgs,
						formData:{
							'post_id': post_id,
							'type': '2'
						},
						 success: function(res) {
							 console.log(res)
							 if(res.statusCode == 200){
								 if(that.videoSrc){//如果有视频
									 // 视频接口
									 uni.uploadFile({
									 	url: 'http://test.api.lanchonggk.com/industry/api/add_video',
									 	filePath: that.videoSrc,
									 	fileType: 'video',
									 	name: 'video',
									 	formData: {
									 	  'post_id': post_id,
									 	  'type': '2'
									 	},
									 	success: (res) => {
									 	  console.log(res);
									 	  if(res.statusCode == 200){
									 		 uni.showToast({
									 			title: '回答成功!',
									 			duration: 2000,
									 			icon: 'none'
									 		 });
									 		 //跳转回答详情页
									 		 uni.navigateTo({
									 			url: '../answerDetail/answerDetail?id='+post_id,
									 		 })
									 	  }
									 	},
										fail: (err) => {
											console.log(err)
										}
									 });
								 }else{
									 uni.showToast({
										title: '回答成功!',
										duration: 2000,
										icon: 'none'
									});
									 // 跳转问题详情页
									 uni.navigateTo({
									 	url: '../answerDetail/answerDetail?id=' + post_id,
									 })
								 }
							 }
						},
						fail: (err) => {
							console.log(err)
						}
					})
				}
			}else{
				uni.showToast({
					title: res.data.msg,
					duration: 2000,
					icon: 'none'
				});
				return false
			}
		})
	},
	//弹出回答框
	toAddAnswer(){
		let that = this
		that.isAddAnswer = true
	},
	//弹出评论框
	toComment(){
		this.isComment = true
	},
	// 获取回答详情
	getAnswerDetail(){
		let that = this
		answer_info({
			answer_id: parseInt(that.answer_id)
		}).then(res => {
			console.log(res)
			if(res.data.code == 1){
				that.answerDetail = res.data.data
				that.question_id = res.data.data.question_id
				question_info({
					member_id: that.member_id,
					access_token: that.access_token,
					question_id: that.question_id
				}).then(res => {
					console.log(res)
					if(res.data.code == 1){
						that.questionDetail = res.data.data
						that.tags = res.data.data.tag_title.split(',')
					}
				})
			}
		})
	}
	}
}
